  --secondary: #004e38;
  --search: #2D2D2D;
  --card: #3c3653;
  --upper-card: #151516;
  --unfocus: #6F2232;
  --disable: #6D6E71;
  --background: #02121D;
  /* --background: #006FB9; */
  --blue: #2a55ca;
  --white: #ffffff;
  --text: #c5d0da;
  --text-secondary: #6d6c70;
  --button: #001487;

  --globalRadius: "20px";
