}

.ml-5 {
  margin-left: 5px;
}

.mr-5 {
  margin-right: 5px;
}

.mr-10 {
  margin-right: 10px;
}

.main {
  margin: 10%;
}

.text-collapse {
  overflow: hidden;
  width: 100%;
  text-overflow: ellipsis;
  max-width: 100vw;
  text-align: center;
}

.address {
  overflow: hidden;
  width: 100%;
  padding-right: 32px;
  text-overflow: ellipsis;
  max-width: 150px;
}


.home {
  position: relative;
  z-index: 1;
}


.home::before {
  content: " ";
  z-index: -1;
  opacity: .4;
  width: 100%;
  position: absolute;
}

.MuiOutlinedInput-root {
  color: #c5d0da;
  color: var(--text)
}

.read-more-button {
  color: #27C498;
  color: var(--primary)
}

input[type=number] {
  -webkit-outer-spin-button: 'none';
}


.dropdown-menu {
  background-color: #02121D;
  background-color: var(--background);
}


.image-cover {
  width: 150px;
  height: 150px;
  border-radius: 50%;

  object-fit: cover;
  object-position: center right;
}

.addButton {
  max-width: 150px;
}

.dropdown-item {
  color: #c5d0da;
  color: var(--text);
}

.Fontcard>.dropdown {
  margin: 10px;

}

.Fontcard {
  box-shadow: 6px 6px 2px 1px rgba(0, 0, 0, 0.4);
  transition: 0.3s;
  border-radius: 5px;
  background-color: #02121D;
  background-color: var(--background);
  border: 1px solid #6D6E71;
  border: 1px solid var(--disable);
}

.Fontcard:hover {
  box-shadow: 3px 3px 2px 1px rgba(0, 0, 0, 0.6);
}

.right {
  float: right;
}


.navbar-collapse {
  text-align: center;
}

.text-collapse {
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 0;
  /* number of lines to show */
  line-clamp: 0;
  -webkit-box-orient: vertical;

}


button:disabled {
  background: #6F2232;
  background: var(--unfocus);
}


.navbar-dark .navbar-toggler-icon {
  color: #004e38;
  color: var(--secondary);
}

body {
  background-color: #02121D;
  color: #c5d0da;
  color: var(--text);
}

* {
  /* font-family: 'VT323', monospace; */
