.btn-success {
  color: #27C498;
  color: var(--primary);
  background-color: transparent;
  text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.3), 0 0 0.45em currentColor;
  box-shadow: inset 0 0 0.5em 0 #27C498, 0 0 0.5em 0 #27C498;
  box-shadow: inset 0 0 0.5em 0 var(--primary), 0 0 0.5em 0 var(--primary);
  border: #27C498 0.125em solid;
  border: var(--primary) 0.125em solid;

}

.btn-success:focus {
  color: #27C498;
  color: var(--primary);
  background-color: transparent;
  text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.3), 0 0 0.45em currentColor;
  box-shadow: inset 0 0 0.5em 0 #27C498, 0 0 0.5em 0 #27C498;
  box-shadow: inset 0 0 0.5em 0 var(--primary), 0 0 0.5em 0 var(--primary);
  border: #27C498 0.125em solid;
  border: var(--primary) 0.125em solid;
}

.btn-success:hover {
  color: #27C498;
  color: var(--primary);
  background-color: transparent;
  text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.3), 0 0 0.45em currentColor;
  box-shadow: inset 0 0 0.5em 0 #27C498, 0 0 0.5em 0 #27C498;
  box-shadow: inset 0 0 0.5em 0 var(--primary), 0 0 0.5em 0 var(--primary);
  border: #27C498 0.125em solid;
  border: var(--primary) 0.125em solid;
}

.show>.btn-success.dropdown-toggle:focus {
  box-shadow: inset 0 0 0.5em 0 #27C498, 0 0 0.5em 0 #27C498;
  box-shadow: inset 0 0 0.5em 0 var(--primary), 0 0 0.5em 0 var(--primary);
}

.show>.btn-success.dropdown-toggle {
  color: #27C498;
  color: var(--primary);
  background-color: transparent;
  text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.3), 0 0 0.45em currentColor;
  box-shadow: inset 0 0 0.5em 0 #27C498, 0 0 0.5em 0 #27C498;
  box-shadow: inset 0 0 0.5em 0 var(--primary), 0 0 0.5em 0 var(--primary);
  border: #27C498 0.125em solid;
  border: var(--primary) 0.125em solid;
}

.show>.btn-success.dropdown-toggle:disabled {
  background-color: transparent;
  box-shadow: none;
  color: #6D6E71;
  color: var(--disable);
  border: #6D6E71 0.125em solid;
  border: var(--disable) 0.125em solid;
  text-shadow: none;
}

