.menu {
  font-family: "Rajdhani", sans-serif;
}

.menu>li {
  position: relative;
}

.menu li a {
  font-family: "Rajdhani",
    sans-serif;
}

menu li ul li a {
  text-align: left;
  text-transform: uppercase;
}


.sub-menu {
  transition: all ease 0.3s;
  background: #0A1F2F;
  width: 100%;
  border-radius: 5px;

}

@media all and (min-width:992px) {
  .sub-menu {
    width: 220px;
    position: absolute;
    top: 90%;
    left: 0;
    transition: all ease 0.3s;
    z-index: 9;
    background: #0A1F2F;
    /* width: 100%; */
    padding: 0;
    -webkit-transform: translateY(35px);
    transform: translateY(35px);
    opacity: 0;
    visibility: hidden;
    border: 1px solid rgba(40, 219, 209, 0.1);
    border-radius: 5px;
  }
}

.menu>li:hover .sub-menu {
  visibility: visible;
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}


@media all and (min-width:992px) {
  .menu>li:hover .sub-menu {
    -webkit-transform: translateY(8px);
            transform: translateY(8px);
  }
}

.menu>li .sub-menu li a {
  padding: 10px 20px;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 16px;
  color: #fff;
  font-weight: 600;
  /* border-bottom: 1px solid rgba(40, 219, 209, 0.1); */
  border-radius: 5px;

  border: 0;
  display: block;
  padding: 10px 25px;
  font-size: 15px;
  font-weight: 700;
  text-transform: capitalize;
